// Styleguide 9.2

@keyframes px2-loading {
	0% {
		transform: scale(0.0);
	}
	100% {
		transform: scale(1.0);
		opacity: 0;
	}
}

.px2-loading{
	clear: both;
	float: none;
	display: flex;
	justify-content: center;
	align-content: center;
	text-align:center;
	padding:80px;
	z-index: 1010000;

	&::before{
		display: block;
		content: "";

		background-color: $px2-main-color; // fallback
		background-color: var(--px2-main-color, $px2-main-color);
		width: 15px;
		height: 15px;
		border-radius: 100%;
		margin: 2px;
		animation-fill-mode: both;

		display: inline-block;
		height: 60px;
		width: 60px;
		animation: px2-loading 1s 0s ease-in-out infinite;

	}
}
